import { h, Component } from 'preact'
import { Link, route } from 'preact-router'
import getUserMedia from 'getusermedia'
import Dropzone from 'react-dropzone'
import CountUp from 'countup.js'
import classNames from 'classnames'
import loadImage from 'blueimp-load-image/js/load-image'
import { connect, events } from 'onfido-sdk-core'
import randomId from '../utils/randomString'
import screenWidth from '../utils/screenWidth'

const Countdown = () => (<span className='onfido-countdown'></span>)

const Spinner = () => {
  return (
    <div className="loader">
      <div className="loader-inner ball-scale-ripple-multiple">
        <div></div>
        <div></div>
        <div></div>
      </div>
    </div>
  )
}

const Processing = () => {
  return (
    <div className='onfido-overlay'>
      <div className='onfido-center'>
        <Spinner />
        <div className='onfido-processing'>Processing your document</div>
      </div>
    </div>
  )
}

const Upload = () => {
  return (
    <div className='onfido-upload'>
      <span className='onfido-icon onfido-icon--upload'></span>
      <p className='onfido-upload-text'>Take a photo with your camera or upload one from your library.</p>
    </div>
  )
}

const NoDocument = () => {
  return (
    <div className='onfido-upload-text onfido-upload-error'>We couldn’t detect a passport or identity card in this image. Please upload another one.</div>
  )
}

export default class Camera extends Component {

  state = {
    noDocument: false,
    uploading: false
  }

  componentWillReceiveProps (nextProps) {
    const { method } = this.props
    const { hasDocumentCaptured, hasFaceCaptured } = nextProps
    const validDocumentCapture = (hasDocumentCaptured && method === 'document')
    const validFaceCapture = (hasFaceCaptured && method === 'face')
    if (validDocumentCapture || validFaceCapture) {
      route('/', true)
    }
  }

  captureImage = () => {
    if (!this.video) return
    const { ratio } = this.dimensions
    const image = this.createBase64(this.canvas, 960, (960 / ratio))
    this.handleImage(image, false)
  }

  createBase64 = (canvas, width, height) => {
    this.ctx = canvas.getContext('2d')
    canvas.width = width
    canvas.height = height
    this.ctx.drawImage(this.video, 0, 0, width, height)
    const image = canvas.toDataURL('image/webp')
    return image
  }

  handleFiles = (files) => {
    this.setState({ uploading: true })
    const options = {
      maxWidth: 960,
      maxHeight: 960,
      canvas: true
    }
    files.map((file) => {
      loadImage(file.preview, (canvas) => {
        events.emit('imageLoaded', canvas)
      }, options)
    })
    events.once('imageLoaded', (canvas) => {
      const base64 = canvas.toDataURL('image/webp')
      this.handleImage(base64)
    })
  }

  handleImage = (image) => {
    const { method, actions, socket } = this.props
    const payload = {
      id: randomId(),
      messageType: method,
      image
    }
    switch (method) {
      case 'document':
        return (() => {
          console.log(payload)
          socket.sendMessage(JSON.stringify(payload))
          actions.documentCapture(payload)
        })()
      case 'face':
        return (() => {
          actions.faceCapture(payload)
          setTimeout(() => actions.setFaceCaptured(true), 1000)
        })()
      default:
        return false
    }
  }

  createStream = (stream) => {
    const { hasDocumentCaptured, method } = this.props
    const { video, capture } = this
    video.src = window.URL.createObjectURL(stream)
    video.onloadedmetadata = (e) => {
      const { clientWidth, clientHeight } = this.video
      this.dimensions = {
        clientWidth,
        clientHeight,
        ratio: (clientWidth / clientHeight)
      }
      video.play()
    }
    if (method === 'document' && !hasDocumentCaptured) {
      capture.start()
    }
  }

  capture = {
    once: () => {
      const options = {
        useEasing : false,
        useGrouping : false
      }
      const countdown = new CountUp(this.countdown, 3, 0, 0, 3, options)
      countdown.start(() => {
        this.video.pause()
        this.captureImage()
      })
    },
    start: () => {
      this.interval = setInterval(() => this.captureImage(), 1000)
    },
    stop: () => {
      clearInterval(this.interval)
    }
  }

  componentDidMount () {
    this.canvas = document.createElement('canvas')
    this.bindGenericEvents()
    getUserMedia((err, stream) => {
      if (err) {

      } else {
        this.createStream(stream)
        this.bindCaptureEvents()
      }
    })
  }

  bindGenericEvents = () => {
    const { actions } = this.props
    events.on('onMessage', (data) => {
      if (data.is_document) {
        actions.setDocumentCaptured(true)
        this.setState({
          uploading: false,
          noDocument: false
        })
      } else {
        this.setState({
          uploading: false,
          noDocument: true
        })
      }
    })
  }

  bindCaptureEvents = () => {
    events.on('onBeforeOpen', () => this.capture.start())
    events.on('onBeforeClose', () => this.capture.stop())
  }

  componentWillUnmount() {
    this.capture.stop()
  }

  renderCaptureButton () {
    return (
      <button id='onfido-capture' className='onfido-btn onfido-btn-primary onfido-btn-capture' onClick={this.capture.once}>
        Take photo
      </button>
    )
  }

  renderPreviews () {
    const { documentCaptures, faceCaptures, method } = this.props
    switch (method) {
      case 'document':
        return documentCaptures.map((file) => <img src={file.base64} />)
      case 'face':
        return faceCaptures.map((file) => <img src={file.base64} />)
      default:
        return false
    }
  }

  renderUploader () {
    const { uploading, noDocument } = this.state
    return (
      <Dropzone
        onDrop={this.handleFiles}
        multiple={false}
        className='onfido-dropzone'
      >
        {uploading && <Processing /> || <Upload />}
        {(!uploading && noDocument) && <NoDocument />}
      </Dropzone>
    )
  }

  renderVideo () {
    return (
      <video
        id='onfido-video'
        className='onfido-video'
        autoplay='autoplay'
        muted={true}
        ref={(video) => { this.video = video }}
      />
    )
  }

  renderNav () {
    return (
      <div class='onfido-actions'>
        <Link href='/' className='onfido-btn-nav onfido-btn-nav--left'>&larr;&nbsp;Back</Link>
        <a rel='modal:close' className='onfido-btn-nav onfido-btn-nav--right'>×&nbsp;Close</a>
      </div>
    )
  }

  render() {
    const { method, supportsGetUserMedia } = this.props
    const useCapture = (supportsGetUserMedia && (screenWidth > 800))
    const showFaceCapture = (useCapture && method === 'face')
    const classes = classNames({
      'onfido-camera': true,
      'onfido-camera-capture': useCapture
    })
    return (
      <div id='onfido-camera' className={classes}>
        {this.renderNav()}
        {showFaceCapture && this.renderCaptureButton()}
        {showFaceCapture && <Countdown ref={(c) => { this.countdown = c }}/>}
        {useCapture && this.renderVideo() || this.renderUploader()}
      </div>
    )
  }

}



